
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'


import axios from 'axios';

export default {
data() {
return {
users: [{id: 1, fonts: '年度积分', integral: 0}, {id: 2, fonts: '活动次数', integral: 0}],
userAbout: {},
dateTime: '',
charts: '',
partAbout: {},
todoList: [],
headName:{},
};
},
components: {
XHeader,
GroupTitle,
Flexbox,
Flexbox,
FlexboxItem,
XButton
},
mounted() {
let datime = new Date().getHours();
if (datime >= 5 && datime < 8) {
this.dateTime = '早上好';
} else if (datime >= 8 && datime < 11) {
this.dateTime = '上午好';
} else if (datime >= 11 && datime < 13) {
this.dateTime = '中午好';
} else if (datime >= 13 && datime < 19) {
this.dateTime = '下午好';
} else {
this.dateTime = '晚上好';
}
this.getHead();
this.$nextTick(function () {

});
this.userName();
this.infoDetail();
/*this.getUserByScoreInfo();
this.getUserByActiveInfo();
this.getScoreByType();*/
},
methods: {
infoDetail() {
axios
.get('/dangjian/pdepartment/queryById', {
params: {
departmentid: this.$store.getters.user.departmentid
}
})
.then(res => {
this.partAbout = res.data;
})
.catch(err => {
console.log(err);
});
axios
.get('/dangjian/pscoredetail/todoList', {
params: {
userId: this.$store.getters.user.userid
}
})
.then(res => {
this.todoList = res.data;
})
.catch(err => {
console.log(err);
});
},
userName() {
axios
.get('/dangjian/ppartymember/queryByUserId', {
params: {
userid: this.$store.getters.user.userid
}
})
.then(res => {
this.userAbout = res.data;
})
.catch(err => {
console.log(err);
});
},
getHead(){
this.$http.get('ppartymember/queryByRoleId',{
params:{
roleid:this.$store.getters.user.roleid
}
}).then(res => {
this.headName = JSON.parse(res.data);
console.log(this.headName);
}).
catch(err =>{
console.log('fail'+err);
})
}
}
};



